body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin:0;
}
.App {
  width:100%;
  height:100vh;
  display:grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: repeat(5,20%);
}


.messenger-container {
    grid-column:1;
    grid-row: 1 / 6 ;
    overflow-y:hidden;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.message-container {
    -webkit-flex:1 1;
            flex:1 1;
    overflow-y:auto;
}

.message-row p {
    font-size:.8em;
    font-weight:100;
}

.message-input {
    display:-webkit-flex;
    display:flex;
    width:100%;
    -webkit-flex-direction:row;
            flex-direction:row;
    font-size:.8em;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.message-input button {
    background:none;
    border:none;
    font-weight:bold;
}

.message-input input {
    -webkit-flex:1 1;
            flex:1 1;
}

.videostream-container {
    height:100vh;
    width:100%;
}

.video-wrapper {
    height: 50%;
}

.video-wrapper h3 {
    margin:0;
    padding:0;
}

.video-wrapper video {
    width:100%;
}
